* {
	box-sizing: border-box;
}

html {
	font-size: 40px;
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

h1 {
	font-weight: normal;
	font-size: 9vw;
	margin: 20vw auto 0;
}

input {
	text-align: center;
	height: 5vh;
	width: 50vw;
	margin: 0 auto;
	padding: 0 20px 5px 20px;
	display: block;
	background: 0;
	outline: transparent;
	border: 0;
	border-bottom: 3px solid aliceblue;
	color: aliceblue;
	font-size: 2vw;
	animation: fadeIn 0.5s linear;
}

.question-name {
	font-size: 3vw;
}

.question-todo {
	font-size: 3vw;
	animation: fadeIn 0.5s linear;
}

.js-greetings {
	font-size: 3vw;
	margin: 0.3vw;
	animation: fadeIn 0.5s linear;
}

button {
	cursor: pointer;
	background: transparent;
	font-size: 0.5vw;
	color: aliceblue;
	border-color: aliceblue;
	border-radius: 100%;
	margin-right: 10px;
	vertical-align: middle;
}

span {
	margin: 0;
	padding: 0;
	font-size: 1.5vw;
	vertical-align: middle;
}

.js-todoList {
	height: 50vw;
	overflow: hidden;
	margin: 0;
	padding: 0 20px 0;
}

li {
	display: inline-block;
	margin: 0.5vw 1vw;
	animation: fadeIn 0.5s linear;
}

body {
	animation: fadeIn 1s linear;
	overflow: hidden;
	min-width: 400px;
	background-color: #353b48;
	color: white;
	/* font-size: 1rem; */
	text-align: center;
	font-family: Roboto, Helvetica, Arial, sans-serif;
}

.js-weather {
	position: absolute;
	top: 20px;
	right: 20px;
}

.js-weather-icon {
	position: absolute;
	top: 65px;
	right: 20px;
	width: 100px;
	height: 100px;
	background-repeat: no-repeat;
}

.clicked {
	color: #2c3e50;
}

.form,
.greetings {
	display: none;
}

.showing {
	display: block;
}

.bgImage {
	min-width: 500px;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	object-fit: cover;
	z-index: -1;
	animation: fadeIn 0.5s linear;
}
